.app {
    padding: 10px;
}

form {
    width: 50%;
}

.form-input {
    margin: 10px 0px;
}

.form-input input, .form-input textarea {
    width: 100%;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: .25rem;
    border: 1px solid lightgrey;
    padding: .375rem .75rem;
    outline: none;
}

button {
    background-color: rgb(7, 46, 65);
    color: white;
    padding: .375rem .75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: .25rem;
    cursor: pointer;
    outline: none;
}

.blog-post__display {
    border: 1px solid lightgrey;
    margin: 5px 0px;
    border-radius: 4px;
    padding: 10px;
}